import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CForm,
  CFormInput,
  CFormSelect,
  CFormLabel,
  CFormFloating,
  CFormTextarea,
  CInputGroup,
  CRow,
} from '@coreui/react'
import React, { useState, useEffect } from 'react'
import axios from "axios";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { useNavigate } from "react-router-dom";

const Report = () => {
  const [valueDate, setDate] = useState(new Date());
  const [valueTime, setTime] = useState(new Date());
  const [shift, setShift] = useState('');
  const [site, setSite] = useState('');
  const [pic, setPic] = useState('');
  const [jenisGiat, setJenisGiat] = useState('');
  const [giat, setGiat] = useState('');
  const [lokasi, setLokasi] = useState('');
  const [objek, setObjek] = useState('');
  const [jumlahKegiatan, setJumlahKegiatan] = useState('');
  const [temuan, setTemuan] = useState('');
  const [jumlahTemuan, setJumlahTemuan] = useState('');
  const [detailTemuan, setDetailTemuan] = useState('');
  const [tindakLanjut, setTindakLanjut] = useState('');
  const [kategoriTemuan, setKategoriTemuan] = useState('');
  const [status, setStatus] = useState('Open');
  const [keterangan, setKeterangan] = useState('');
  const [image, setImage] = useState("");
  const [location, setLocation] = useState({
    loaded: false,
    coordinates: { lat: "", lng: "" },
  });

  const navigate = useNavigate();

  const onSuccess = (location) => {
    setLocation({
        loaded: true,
        coordinates: {
            lat: location.coords.latitude,
            lng: location.coords.longitude,
        },
    });
  };

  const onError = (error) => {
    setLocation({
        loaded: true,
        error: {
            code: error.code,
            message: error.message,
        },
    });
  };

  useEffect(() => {
    if (!("geolocation" in navigator)) {
        onError({
            code: 0,
            message: "Geolocation not supported",
        });
    }

    navigator.geolocation.getCurrentPosition(onSuccess, onError);
  }, []);

  let mobileNumber = '+6282134272627';
  let message = "halo mas dhes, tes dari website";

  const ReactWhatsapp = (e) => {
    e.preventDefault();
    let number = mobileNumber.replace(/[^\w\s]/gi, "").replace(/ /g, "");
    let url = `https://web.whatsapp.com/send?phone=${number}`;
    url += `&text=${encodeURI(message)}&app_absent=0`;
    window.open(url);
  }
  
  const saveReport = async (e) => {
    e.preventDefault();
    const formData= new FormData();
      formData.append("dates", valueDate);
      formData.append("shift", shift);
      formData.append("site", site);
      formData.append("pic", pic);
      formData.append("jenis_giat", jenisGiat);
      formData.append("giat", giat);
      formData.append("lokasi", lokasi);
      formData.append("objek", objek);
      formData.append("jumlah_kegiatan", jumlahKegiatan);
      formData.append("temuan", temuan);
      formData.append("jumlah_temuan", jumlahTemuan);
      formData.append("detail_temuan", detailTemuan);
      formData.append("tindak_lanjut", tindakLanjut);
      formData.append("kategori_temuan", kategoriTemuan);
      formData.append("status", status);
      formData.append("keterangan", keterangan);
      formData.append("image", image);
      formData.append("latitude", location.coordinates.lat);
      formData.append("longitude", location.coordinates.lng);

      try {
        await axios.post("http://localhost:5000/reports/", formData, {
          headers: {
            "Content-type": "multipart/form-data",
          },
        });      
        navigate("/dashboard");
      } catch (error) {
        console.log(error);
      }
  };

  return (
    <CRow>
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <strong>Security</strong> <small>Report</small>
          </CCardHeader>
          <CCardBody>
            <p className="text-medium-emphasis medium">
              Mohon lengkapi data dibawah ini
            </p>
            <CForm validated={true} onSubmit={saveReport}>
              <CRow>
                <CCol xs="auto">
                  <DatePicker id='dates' selected={valueDate} dateFormat="dd/MM/yyyy" onChange={(e) => setDate(e)} disabled readOnly/>
                </CCol>
                <CCol xs>
                  <DatePicker
                  id='time' 
                  selected={valueTime}
                  onChange={(e) => setTime(e)}
                  showTimeSelect 
                  showTimeSelectOnly 
                  disabled readOnly
                  dateFormat="HH:mm"
                  />
                </CCol>
              </CRow>
              <CRow>
                <p> </p>
              </CRow>
              <CRow>
                <CCol xs>
                  <CFormSelect 
                    id="shift"
                    value={shift} 
                    onChange={(e) => setShift(e.target.value)} 
                    aria-describedby="validationCustom04Feedback"
                    feedbackValid="Sesuai!" 
                    feedbackInvalid="Masukkan Shift" 
                    required
                  >
                    <option value="">Pilih Shift...</option>
                    <option value="Shift 1">Shift 1</option>
                    <option value="Shift 2">Shift 2</option>
                  </CFormSelect>
                </CCol>
                <CCol xs>
                  <CFormSelect 
                    id="site" 
                    value={site} 
                    onChange={(e) => setSite(e.target.value)} 
                    aria-describedby="validationCustom04Feedback" 
                    feedbackValid="Sesuai!" 
                    feedbackInvalid="Masukkan Site" 
                    required
                  >
                    <option value="">Pilih Site...</option>
                    <option value="BMO 1">BMO 1</option>
                    <option value="BMO 2">BMO 2</option>
                    <option value="GMO">GMO</option>
                    <option value="HO">HO</option>
                    <option value="LMO">LMO</option>
                    <option value="PMO">PMO</option>
                    <option value="SMO">SMO</option>
                    <option value="SUARAN">SUARAN</option>
                  </CFormSelect>
                </CCol>
                <CCol xs>
                  <CFormSelect 
                    id="pic" 
                    value={pic} 
                    onChange={(e) => setPic(e.target.value)} 
                    aria-describedby="validationCustom04Feedback"
                    feedbackValid="Sesuai!"  
                    feedbackInvalid="Masukkan PIC" 
                    required
                  >
                    <option value="">Pilih PIC...</option>
                    <option value="RONI">RONI</option>
                    <option value="SUKENI">SUKENI</option>
                    <option value="SULAIMAN">SULAIMAN</option>
                    <option value="SUPRI">SUPRI</option>
                    <option value="TARMUTTI">TARMUTTI</option>
                  </CFormSelect>
                </CCol>
              </CRow>
              <CRow>
                <p>  </p>
                <CCol xs>
                  <CFormSelect 
                    id="jenis_giat" 
                    value={jenisGiat} 
                    onChange={(e) => setJenisGiat(e.target.value)} 
                    aria-describedby="validationCustom04Feedback"
                    feedbackValid="Sesuai!"  
                    feedbackInvalid="Masukkan Jenis Giat" 
                    required                 
                  >
                    <option value="">Pilih Jenis Giat...</option>
                    <option value="Patroli">Patroli</option>
                    <option value="Pemeriksaan">Pemeriksaan</option>
                    <option value="Pengawalan">Pengawalan</option>
                    <option value="Situasi">Situasi</option>
                  </CFormSelect>
                </CCol>
                <CCol xs>
                  <CFormSelect 
                    id="giat" 
                    value={giat} 
                    onChange={(e) => setGiat(e.target.value)} 
                    aria-describedby="validationCustom04Feedback"
                    feedbackValid="Sesuai!"  
                    feedbackInvalid="Masukkan Giat" 
                    required
                  >
                    <option value="">Pilih Giat...</option>
                    <option value="Patroli Aset">Patroli Aset</option>
                    <option value="Patroli Komunikasi">Patroli Komunikasi</option>
                    <option value="Patroli PIT dan Disposal">Patroli PIT dan Disposal</option>
                    <option value="Patroli WMP dan Void">Patroli WMP dan Void</option>
                    <option value="Pemeriksaan Barang">Pemeriksaan Barang</option>
                    <option value="Pemeriksaan Helm">Pemeriksaan Helm</option>
                    <option value="Pemeriksaan SID">Pemeriksaan SID</option>
                    <option value="Pengawalan Warga">Pengawalan Warga</option>
                    <option value="Situasi Menonjol">Situasi Menonjol</option>
                  </CFormSelect>
                </CCol>
              </CRow>
              <CRow>
                <p>  </p>
                <CCol xs>
                  <CFormFloating>
                    <CFormInput 
                      type='text' 
                      id="lokasi" 
                      placeholder="Lokasi" 
                      value={lokasi} 
                      onChange={(e) => setLokasi(e.target.value)}
                      feedbackValid="Sesuai!"
                      feedbackInvalid="Masukkan Lokasi"
                      required
                    />
                    <CFormLabel>Lokasi</CFormLabel>
                  </CFormFloating>
                </CCol>
                <CCol>
                  <CFormFloating>
                    <CFormInput 
                      type='text' 
                      id="objek" 
                      placeholder="Objek" 
                      value={objek} 
                      onChange={(e) => setObjek(e.target.value)}
                      feedbackValid="Sesuai!"
                      feedbackInvalid="Masukkan Objek"
                      required
                    />
                    <CFormLabel>Objek</CFormLabel>
                  </CFormFloating>
                </CCol>
              </CRow>
              <CRow>
                <p>  </p>
                <CCol xs>
                  <CFormSelect 
                    id="jumlah_kegiatan" 
                    value={jumlahKegiatan} 
                    onChange={(e) => setJumlahKegiatan(e.target.value)} 
                    aria-describedby="validationCustom04Feedback" 
                    feedbackValid="Sesuai!"
                    feedbackInvalid="Masukkan Jumlah Kegiatan" 
                    required
                  >
                      <option value="">Pilih Jumlah Kegiatan...</option>
                      <option value="0">0</option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                    </CFormSelect>
                </CCol>
                <CCol xs>
                  <CFormSelect 
                    id="temuan" 
                    value={temuan} 
                    onChange={(e) => setTemuan(e.target.value)} 
                    aria-describedby="validationCustom04Feedback"
                    feedbackValid="Sesuai" 
                    feedbackInvalid="Masukkan Temuan" 
                    required
                  >
                      <option value="">Pilih Temuan...</option>
                      <option value="Aman">Aman</option>
                      <option value="Kondisi Tidak Aman">Kondisi Tidak Aman</option>
                      <option value="Tindakan Tidak Aman">Tindakan Tidak Aman</option>
                    </CFormSelect>
                </CCol>
                <CCol xs>
                  <CFormSelect 
                    id="jumlah_temuan" 
                    value={jumlahTemuan} 
                    onChange={(e) => setJumlahTemuan(e.target.value)} 
                    aria-describedby="validationCustom04Feedback" 
                    feedbackValid="Sesuai!"
                    feedbackInvalid="Masukkan Jumlah Temuan" 
                    required
                  >
                      <option value="">Pilih Jumlah Temuan...</option>
                      <option value="0">0</option>
                      <option value="1">1</option>
                      <option value="2">2</option>
                    </CFormSelect>
                </CCol>
              </CRow>
              <CRow>
                <p>  </p>
                <CCol xs>
                  <CFormFloating>
                    <CFormInput 
                      type='text' 
                      id="detail_temuan" 
                      placeholder="Detail Temuan" 
                      value={detailTemuan} 
                      onChange={(e) => setDetailTemuan(e.target.value)} 
                      feedbackValid="Sesuai!"
                      feedbackInvalid="Masukkan Detail Temuan"
                      required
                    />
                    <CFormLabel>Detail Temuan</CFormLabel>
                  </CFormFloating>
                </CCol>
                <CCol>
                  <CFormFloating>
                    <CFormInput 
                      type='text' 
                      id="tindak_lanjut" 
                      placeholder="Tindak Lanjut" 
                      value={tindakLanjut} 
                      onChange={(e) => setTindakLanjut(e.target.value)} 
                      feedbackValid="Sesuai!"
                      feedbackInvalid="Masukkan Tindak Lanjut"
                      required
                    />
                    <CFormLabel>Tindak Lanjut</CFormLabel>
                  </CFormFloating>
                </CCol>
              </CRow>
              <CRow>
                <p>  </p>
                <CCol xs>
                  <CFormSelect 
                    id="kategori_temuan" 
                    value={kategoriTemuan} 
                    onChange={(e) => setKategoriTemuan(e.target.value)} 
                    aria-describedby="validationCustom04Feedback"
                    feedbackValid="Sesuai!" 
                    feedbackInvalid="Masukkan Kategori Temuan" 
                    required
                  >
                      <option value="">Pilih Kategori Temuan...</option>
                      <option value="Merah">Merah</option>
                      <option value="Kuning">Kuning</option>
                      <option value="Hijau">Hijau</option>
                    </CFormSelect>
                </CCol>
                <CCol xs>
                  <CFormSelect id="status" value={status} onChange={(e) => setStatus(e.target.value)} disabled readOnly>
                    <option>Open</option>
                  </CFormSelect>
                </CCol>
              </CRow>
              <p>  </p>
              <CRow>
                <CCol xs>
                  <CFormFloating>
                    <CFormTextarea 
                      type='text' 
                      id="keterangan" 
                      placeholder="Keterangan" 
                      value={keterangan} 
                      onChange={(e) => setKeterangan(e.target.value)}
                      feedbackValid="Sesuai!"
                      feedbackInvalid="Masukkan Keterangan (jika tidak ada masukkan TIDAK ADA)"
                      required  
                    />
                    <CFormLabel>Keterangan</CFormLabel>
                  </CFormFloating>
                </CCol>
              </CRow>
              <CRow>
              <p>  </p>
              <CCol xs>
                  <CInputGroup className="mb-3">
                      <CFormInput 
                        type="file" 
                        id="image"
                        onChange={(e) => setImage(e.target.files[0])} 
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Bukti Kegiatan"
                        required  
                      />
                  </CInputGroup>
              </CCol>
              </CRow>
              <p>  </p>
              <CRow>
                <CCol xs>
                  <CFormLabel
                    onChange={(e) => setLocation(e.target.value)} >
                    <p>Latitude: {location.coordinates.lat} </p>
                    <p> </p>
                    <p>Longitude: {location.coordinates.lng} </p>
                  </CFormLabel>
                </CCol>
              </CRow>
              <CRow>
              <p>  </p>
              <CCol xs>
                <CButton 
                  type='submit'
                  color="primary" 
                  shape='rounded-pill'
                  disabled={!shift || !site || !pic || !jenisGiat || !giat || !lokasi || !objek || !jumlahKegiatan || !temuan || !jumlahTemuan || !detailTemuan || !tindakLanjut || !kategoriTemuan || !keterangan || !image}>
                  Submit
                </CButton>
              </CCol>
              </CRow>
            </CForm>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )}

  export default Report;